.app-download {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  .download {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 20px;
    color: white;
    padding: 10px 25px;
    background-color: #363B3F;
    text-decoration: none;
    border-radius: 15px;

    &:hover {
      background-color: #2E3437;
    }

    i {
      padding-right: 10px;
    }
  }
}

.download-popover {
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  align-items: stretch;
  width: 200px;
  a {
    padding: 10px;
    &:hover {
      background-color: #7f7f7f;
      color: white;
    }
    color: black;
    text-decoration: none;
    i {
      margin-right: 10px;
    }
  }
}